.ball {
  width: 2rem;
  height: 2rem;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 0.13rem solid #f15d57;
  border-radius: 50%;
  margin: 0.8rem;
  color: #f15d57;
}

p {
  font-size: 1rem;
}

.back-ball {
  border: 0.13rem solid #44bbd5;
  color: #44bbd5;
}

.active {
  background-color: #f15d57;
  color: white;
}

.back-active {
  background-color: #44bbd5;
  color: white;
}
